<template>
  <v-col :sm="sm" :xs="xs" :md="md" :lg="lg" :cols="cols">
    <v-tooltip
      top
      max-width="250"
      nudge-top="-15"
      transition="fade-transition"
      open-delay="500"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-card tile v-bind="attrs" v-on="on">
          <v-card-text>
            <div>
              <v-checkbox
                v-if="type"
                v-model="selectedBooks"
                :value="book._id"
                dense
                hide-details
                class="ma-0 pa-0"
                :label="`#${book.bookNumber}`"
              ></v-checkbox>
              <span v-else>#{{ book.bookNumber }}</span>
              <router-link v-if="!type" :to="`/books/${book._id}`">
                <v-icon class="float-right pointer" size="20" color="primary"
                  >mdi-dots-vertical</v-icon
                >
              </router-link>
              <v-icon
                v-if="!type"
                @click="showQrCode(book._id)"
                class="float-right pointer mr-1"
                size="20"
                color="primary"
                >mdi-qrcode</v-icon
              >
            </div>
            <div class="text--primary force-single-line">
              {{ book.name }}
            </div>
            <div
              :class="['force-single-line', book.author ? '' : 'not-assigned']"
            >
              {{ book.author ? book.author : "No author assigned" }}
            </div>
            <div class="force-single-line">{{ book.type }}</div>
            <div class="force-single-line">
              Shelf number
              {{ book.shelfNumber ? `S${book.shelfNumber}` : "not assigned" }}
            </div>
            <div v-if="!type">
              Purchased on {{ new Date(book.dateAdded) | formatDate }}
            </div>
            <div v-else-if="type === 'return'">
              Distributed on
              {{ new Date(book.lastDistributedDate) | formatDate }}
            </div>
            <div v-else>
              <span v-if="book.status === 2"
                >Distributed on
                {{ new Date(book.lastDistributedDate) | formatDate }}</span
              >
              <span v-else-if="book.lastDistributedDate"
                >Last distributed on
                {{ new Date(book.lastDistributedDate) | formatDate }}</span
              >
              <span v-else>Not yet distributed</span>
            </div>

            <v-badge
              dot
              inline
              left
              tile
              :color="statusColors[book.status]"
              class="stock-badge"
            >
              <div
                :class="[
                  'force-single-line',
                  `${statusColors[book.status]}--text`,
                ]"
              >
                <span v-if="book.status === 1">In stock</span>
                <span v-else-if="book.status === 2">
                  {{
                    book.distributedTo
                      ? `# ${book.distributedTo.memberNumber} ${book.distributedTo.name}`
                      : "Out of stock"
                  }}
                </span>
                <span v-else>Removed</span>
              </div>
            </v-badge>
          </v-card-text>
        </v-card>
      </template>
      # {{ book.bookNumber }} {{ book.name }}
    </v-tooltip>
  </v-col>
</template>

<script>
import { toRefs } from "@vue/composition-api";
import store from "../store";

const showQrCode = (bookId) => {
  store.books.state.showQrCode = true;
  store.books.state.qrCodeBook = bookId;
};

export default {
  props: ["lg", "md", "sm", "xs", "cols", "book", "type"],
  setup(props) {
    let data = {
      statusColors: { 1: "green", 2: "orange", 3: "grey" },
    };
    if (props.type === "distribute") {
      data = {
        ...data,
        ...toRefs(store.distribute.state),
      };
    } else if (props.type === "return") {
      data = {
        ...data,
        ...toRefs(store.returnBook.state),
      };
    }
    return { ...data, showQrCode };
  },
};
</script>